      <a-button type="primary" @click="openModal = true">
        <template #icon>
          <ImportOutlined/>
        </template>
      </a-button>
      <span class="font-bold text-gray-500 text-sm">
        ÁºñÂè∑Ôºö„Äê{{ props.course_id }}„Äë
        ËØæÂêçÔºö„Äê{{ props.course_name }}„Äë
        Âú∫È¶ÜÔºö„Äê{{ props.venue_name }}„Äë
      </span>
    </div>
    <div class="mt-1">
      <a-modal v-model:visible="openModal" title="ÈÄâÊã©ËØæÁ®ã" centered width="800px">
        <a-input-search
            v-model:value="title"
            enter-button
            @search="fillData"
            class="mb-2"
            placeholder="ËØ∑ËæìÂÖ•ËØæÁ®ã"
        />
        <a-table :dataSource="dataSource" :columns="columns" bordered size="small">
          <template #bodyCell="{ column, record }">
            <template v-if="column.key === 'id'">
